body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

select.form-control,
textarea.form-control,
input.form-control {
  font-size: 16px;
}
input[type=file] {
  width: 100%;
}

.LoaderButton .spinning.glyphicon {
    margin-right: 7px;
    top: 2px;
    -webkit-animation: spin 1s infinite linear;
            animation: spin 1s infinite linear;
  }
  @-webkit-keyframes spin {
    from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
    to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
  }
  @keyframes spin {
    from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
    to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
  }

@media all and (min-width: 480px) {
    .Login {
      padding: 60px 0;
    }

    .Login form {
      margin: 0 auto;
      max-width: 320px;
    }
  }

@media all and (min-width: 480px) {
    .Signup {
      padding: 15vh 0;
    }

    .Signup form {
      margin: 0 auto;
      max-width: 30vw;
    }
  }

.Message {
    line-height: 1.5em;
    font-size: 1.5em;
  }

  .Message.log {
    color: #bbb;
  }

  .Message .author {
    font-weight: bold;
    color: #888;
    margin: 0 5px 0 0;
  }

  .Message.me .author {
    color:#000080;
  }

  .Message .author {
    color: #D14836;
  }

.MessageList {
    overflow-y: auto;
    padding: 4px;
  }

.MessageForm {
    display: -webkit-flex;
    display: flex;
    background: #ccc;
  }

  .MessageForm .input-container {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 1px;
  }

  .MessageForm .button-container {
    -webkit-flex: 0 0 6em;
            flex: 0 0 6em;
    margin: 1px 1px 1px 0;
  }

  .MessageForm input {
    background: #fff;
    border-radius: 6px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: inherit;
    height: 100%;
    outline: none;
    width: 100%;
  }

  .MessageForm button {
    background: #0066cc;
    border-radius: 6px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: inherit;
    color:white;
    height: 100%;
    outline: none;
    width: 100%;
  }

  .MessageForm input {
    padding: 0 0 0 8px;
  }

.Main {
    border: 1.5px solid #ccc;
    border-radius: 3px;
    height: 88vh;
    width: 98%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: auto;
    font-size: 13x;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
  }

  .Main .MessageList {
    box-sizing: border-box;
    -webkit-flex: 1 1;
            flex: 1 1;
  }

  .Main .MessageForm {
    background: #eee;
    border-top: 1px solid #ccc;
    border-radius: 0 0 3px 3px;
    box-sizing: border-box;
    -webkit-flex: 0 0 30px;
            flex: 0 0 30px;
  }

  .Main-logo {
    -webkit-animation: Main-logo-spin infinite 20s linear;
            animation: Main-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
  }

  .Main-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .Main-link {
    color: #61dafb;
  }

  @-webkit-keyframes Main-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  @keyframes Main-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

.App .navbar {
    background-color: #808080;
    height: 8vh;
    margin-top: 0.9vh;
    margin-left: 1.1vw;
    margin-right: 1.1vw;
    border-radius: 3px;
}

.App h1 b {
    position: relative;
    margin-left: 2vw;
    bottom: 0.9vh;
    color: white;
    font-size: 3rem;
}

.App h1 img {
    position: relative;
    margin-left: 1.8rem;
    bottom: 1.3vh;
}

.App h1 .SignUp{
    position: absolute;
    top: 2.5vh;
    right: 3vw;
    color: white;
    font-size: 2.5rem;
}

.App h1 .LogOut{
    position: absolute;
    top: 2.5vh;
    right: 3vw;
    color: white;
    font-size: 2.5rem;
}

